.navBarContainer {
  width: 100%;
  height: 84px;
  padding-top: 16px;
  margin-bottom: 24px;
  display: flex;
  align-items: center;
  position: relative;

  @media (max-width: 767px) {
    height: 56px;
    padding-top: 16px;
  }

  span {
    font-size: 24px;
    line-height: 1.12;
    font-weight: 600;
    position: absolute;
    left: 0;
    right: 0;
    text-align: center;
    z-index: -1;
  }
}

.icon {
  width: 30px;
  height: 30px;
  cursor: pointer;

  @media (max-width: 767px) {
    width: 24px;
    height: 24px;
  }
}
