.container {
  padding: 0 16px;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.outer {
  width: 100%;
  border-radius: 6px;
  height: 12px;
  background-color: #f5f5f5;
}

.inner {
  background: linear-gradient(45deg, #0ecffe 50%, #07a6f1);
  height: 100%;
  border-radius: 6px;
  transition: 0.3s all ease-in-out;
}

.display {
  font-size: 20px;
  line-height: 1;
  color: #0c0c0c;
  margin-left: 16px;
  flex-shrink: 0;

  span {
    color: gray;
    font-size: 16px;
    line-height: 1.2;
    vertical-align: text-top;
    font-weight: 500;

    &.post {
      vertical-align: sub;
      font-size: 18px;
    }
  }
}
